import rickroll from './rickroll.mp3';
import './App.css';
import {useState, useRef} from 'react';
import { initializeApp } from "firebase/app";
import { getDatabase, ref, onValue, update, set } from "firebase/database";
import {Button} from '@rmwc/button'
import {createDialogQueue} from '@rmwc/dialog'
import '@rmwc/button/styles';
// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Your web app's Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyC3hbDjJP4WlbIUTs3P4D2GPW7teRkofbM",
  authDomain: "rickroll-e37a1.firebaseapp.com",
  databaseURL: "https://rickroll-e37a1-default-rtdb.asia-southeast1.firebasedatabase.app",
  projectId: "rickroll-e37a1",
  storageBucket: "rickroll-e37a1.appspot.com",
  messagingSenderId: "557345821992",
  appId: "1:557345821992:web:4a8246159f298f175b6c1d"
};

// Initialize Firebase
initializeApp(firebaseConfig);
const db = getDatabase();
const dbRef = ref(db, 'enabled')
update(dbRef, {
  'state': 'false'
})

/*set(dbRef, {
  'state': 'false'
});*/


function App() {
  const [page, setPage] = useState(localStorage.getItem('authed') ? 'terminal' : 'client')
  const [initialized, setInitialized] = useState(false)
  const { prompt } = createDialogQueue();
  const audio = useRef()
  const setPlay = () => {
    set(dbRef, {
    'state': 'true',
    
    })
  }
  const setPause = () => set(dbRef, {
    'state': 'false'
  })

  const setSync = () => update(dbRef, {
    'state': 'true',
    'start': (new Date()).getTime() + 2000
  })
  
  function handleClick(){
    
    setTimeout(() => setInitialized(true), 500)
    onValue(dbRef, (snapshot) => {
      if(page !== "client") return;
      const data = snapshot.val();
      
        if(data.state === "false") return audio.current.pause()
        if(!data.start) return audio.current.play();
        audio.current.currentTime = 0;
        let k = Number(data.start)
        let ref = setInterval(() => {
          console.log('pending', (new Date()).getTime(), k)
          if((new Date()).getTime() - k > 100) return;
          if((new Date()).getTime() > k) {audio.current.play();
          clearInterval(ref)}
        }, 10)


      
    });
  }
  return (
    <div style={{margin: '16px'}}>
     
      
      {page === "terminal" ? 
       
      <div>
        <Button onClick={() => page === "terminal" && setPage("client")} style={{'margin-bottom': '8px'}} raised>Terminal</Button><br></br>
        <Button onClick={setPlay} outlined style={{ 'margin-bottom': '8px'}}>$ SUDO RICKROLL START</Button>
        <Button onClick={setPause} outlined style={{'margin-left': '8px', 'margin-bottom': '8px'}}>$ SUDO RICKROLL STOP</Button><br></br>
        <Button onClick={setSync} outlined >$ SUDO RICKROLL SYNC</Button>
      </div> 
      : 
      <div>
        <Button onClick={() => {
        page === "client" && (localStorage.getItem('authed') || prompt() === "youknowtherules") && (setPage('terminal') || localStorage.setItem('authed', 'terminal'));
        
        
      }} style={{'margin-bottom': '8px'}} raised>Client</Button><br></br>
        {!initialized && <Button onClick={handleClick} outlined>$ SUDO Initialize</Button>}
        {initialized && <Button disabled outlined>Initialized</Button>}
        <audio loop ref={audio}>
         <source src={rickroll} type="audio/mpeg" id="audio" ></source>
         </audio>
      </div>
      
  
      }

    </div>
    
  );
}

export default App;
